function register(component, name) {
  UIkit.component(name, component);
}

import UIkit from 'uikit/src/js/api/index';
import Core from 'uikit/src/js/core/core';
import boot from 'uikit/src/js/api/boot';
import * as coreComponents from './core';
import * as components from './components';
import {each} from 'uikit/src/js/util/lang';

// register components
each(coreComponents, register);
each(components, register);

// core functionality
UIkit.use(Core);

boot(UIkit);

export default UIkit
